.main-photo {
	
	padding-top: 40px;
	background-color: #FFF;
	
	&__title {
		
		margin: 0;
		padding-bottom: 40px;
		font-size: 18px;
		color: #000;
    text-transform: uppercase;
    text-align: center;
	}

	&__review {
		
    margin: 0;
    margin-bottom: 20px;
    padding-bottom: 30px;
    padding-left: 25px;
    padding-right: 25px;
    font-size: 14px;
    color: #696969;
    border-bottom: 1px solid @gray-lightest;
    text-transform: uppercase;
    text-align: center;
	}
}


.photo-img {
    
  margin-bottom: 20px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 10px;
  background-color: #FFF;
  border-bottom: 1px solid @gray-lightest;
    
  & img{

    width: 280px;
    height: 280px;
    padding-bottom: 10px;
  }

  & h4 {
      
    font-size: 14px;
    line-height: 14px;
    color: #000;
    text-transform: uppercase;
    text-align: left;
  }

  & figure {

    margin: 0;
  }

  &:last-child {
    
    margin-bottom: 0;
  }
}

.photo-img__author {
    
  font-size: 14px;
  line-height: 14px;
  color: #696969;
  text-align: left;
}

.photo-img__like {
    
  position: relative;
  padding-left: 35px;
  font-size: 16px;
  font-weight: bold;
  color: #000;
  text-align: left;
    
  & svg {
      
    display: block;
    width: 20px;
    height: 22px;
    margin-top: -24px;
  }
}

.video {
  
  padding-top: 30px;
  padding-bottom: 50px;
  background-color: #FFF;
  
  &__title {
    
    margin: 0;
    padding-bottom: 30px;
    font-size: 18px;
    color: #000;
    text-transform: uppercase;
    text-align: center;
  }

  &__review {
    
    margin: 0;
    margin-bottom: 20px;
    padding-bottom: 20px;
    padding-left: 25px;
    padding-right: 25px;
    font-size: 14px;
    color: #696969;
    text-transform: uppercase;
    text-align: center;
  }

  & img {
    
    margin: 0;
    padding: 0;
    width: 100%;
    max-width: 320px;
    height: auto;
  }
}

.button-group {
  
  position: relative;
  width: 280px;
  height: 100px;
  margin: 0 auto;
  margin-top: -45px;
  background-color: #f2f2f2;
}

.video-tuning1,
.video-tuning2,
.video-tuning3,
.video-tuning4 {
  
  padding: 0;
  font-size: 0;
  border: none;
  outline: none;
}

.video-tuning1 {
  
  position: relative;
  width: 280px;
  height: 40px;
  background-color: rgba(0, 0, 0, .4);
  
  &::before {
    
    content: "";
    position: absolute;
    top: 19px;
    left: 25px;
    display: block;
    width: 235px;
    height: 3px;
    background-color: rgba(255, 255, 255, 0.4);
  }

  &::after {
    
    content: "";
    position: absolute;
    top: 16px;
    left: 100px;
    display: block;
    width: 10px;
    height: 10px;
    -webkit-border-radius: 50%;
            border-radius: 50%;
    background-color: rgba(255, 255, 255, 0.8);
  }
}

.video-tuning2 {
  
  position: absolute;
  top: 57px;
  left: 20px;
  display: block;
  background-color: transparent;
}


.video-tuning3 {
  
  position: absolute;
  display: inline-block;
  left: 185px;
  top: 58px;
  background-color: transparent;
}

.video-tuning4 {
  
  position: absolute;
  display: inline-block;
  left: 240px;
  top: 58px;
  background-color: transparent;
}

