.clearfix() {
  &::after {
    content: "";
    display: table;
    clear: both;
  }
}

.no-outline() {
  outline: none;
  background: transparent;
  
  &::before {
    display: none;
  }
}

.list() {
  margin: 0;
  padding: 0;
  list-style: none;
}