@media (max-width: 767px) {

.preview {
	
	background-color: #FFF;
  
  &__title,
  &__description {
    display: block;
    margin: 0;
    padding-left: 30px;
    padding-right: 30px;
    text-transform: uppercase;
    text-align: center;
  }

  &__title {
    padding-bottom: 20px;
    font-size: 18px;
    font-weight: bold;
    line-height: 26px;
  }

  &__description {
    position: relative;
    padding-bottom: 35px;
    font-size: 14px;
    line-height: 24px;
    
    &:after {
      content: url(../img/bg-triangle.svg);
      position: absolute;
      top: 80px;
      left: 0;
      bottom: -60px;
      display: block;
      width: 320px;
    }
  }
}

.description-1,
.description-2 {
  display: block;
  padding-top: 40px;
  background-color: #81b3d2;
  
  &-item {
    width: 320px;
    padding-left: 25px;
    padding-right: 25px;
    text-align: center;
    color: #fff;
    text-transform: uppercase;
    
    &__preview {
      margin-bottom: 40px;
    }
  }

  & img {
    width: 320px;
    height: auto;
    margin-bottom: -5px;
    background-color: #fff;
  }
}

.description-2 {
  padding-top: 5px;
}

.description-3 {
  display: block;
  padding-top: 20px;
  padding-left: 15px;
  padding-right: 15px;
  background-color: @gray-lightest;
  text-align: center;
  text-transform: uppercase;
  
  & span,
  & br {
    display: none;
  }
  
  &-item {
    width: 320px;
    width: 290px;
    padding-top: 0;
    padding-bottom: 15px;
    padding-left: 10px;
    padding-right: 10px;
    border-bottom: 1px solid @gray-light;
    
    &:last-child {
      border-bottom: none;
    }
    
    &__preview {
      
      font-size: 14px;
    }
  }
}

.own {
  display: block;
  padding-bottom: 30px;
  background-color: #fff;
  
  &-item {
    position: relative;
    padding-left: 70px;
    padding-right: 25px;
    padding-top: 35px;
    padding-bottom: 0;
    text-align: left;
    text-transform: uppercase;
  }

  &-item::before {
    content: "";
    position: absolute;
    top: 30px;
    left: 20px;
    display: block;
    width: 35px;
    height: 35px;
    background-repeat: no-repeat;
  }

  &-item__1::before {
    background-image: url(../img/icon-house.svg);
  }

  &-item__2::before {
    background-image: url(../img/icon-gift.svg);
  }

  &-item__3::before {
    background-image: url(../img/icon-burger.svg);
  }

  &__title,
  &__review {
    margin: 0;
  }

  &__title {
    padding-bottom: 20px;
  }

  &__review {
    font-size: 14px;
  }
}
}




@media (min-width: @screen-md) {
  .preview {
    background-color: #FFF;
    text-align: center;
    text-transform: uppercase;
    
    &__title {
      display: block;
      margin: auto;
      padding-top: 50px;
      padding-left: 150px;
      padding-right: 150px;
      font-size: 21px;
      font-weight: bold;
      
      & br {
        display: none;
      }
    }

    &__description:after {
      display: none;
    }

    &__description {
      margin-top: 20px;
      padding-bottom: 40px;
    }
  }
}

.description-1,
.description-2 {
  
  display: flex;  
  padding-top: 0;
  color: #fff;

  &-item {
    width: 256px;
    padding-top: 30px;
    padding-left: 35px;
    padding-right: 35px;
    background-color: #81b3d2;
  }
  
  & .img {
    width: 512px;
    margin-bottom: -5px;
  }
  
  & img {
    width: 512px;
    height: 256px;
  }
}

.description-2 {
  -webkit-flex-direction: row-reverse;
      -ms-flex-direction: row-reverse;
          flex-direction: row-reverse;
}

.description-3 {
  display: flex;
  margin: 0;
  padding: 0;
  background-color: #eee;
  
  & span,
  & br {
    display: inline-block;
  }
  
  &-item {
    width: 33.3%;
    margin-bottom: 0;
    padding-top: 40px;
    padding-bottom: 40px;
    padding-left: 30px;
    padding-right: 30px;
    border-bottom: none;
    
    &:nth-child(n+2) &__title {
      padding-left: 40px;
      padding-right: 40px;
    }    
  }
}

.own {
  display: flex;
  
  &-item {
    position: relative;
    padding-left: 25px;
    padding-right: 25px;
    padding-top: 160px;
    padding-bottom: 65px;
    text-align: center;
    
    & br {
      display: none;
    }
    
    &::before {
      content: "";
      position: absolute;
      top: 60px;
      left: 95px;
      display: block;
      width: 70px;
      height: 70px;
      background-repeat: no-repeat;
    }

    &__1::before {
      background-image: url(../img/icon-house.svg);
    }

    &__2::before {
      background-image: url(../img/icon-gift.svg);
    }

    &__3::before {
      background-image: url(../img/icon-burger.svg);
    }
  }

  &-item:nth-child(2) {
    -webkit-order: 3;
        -ms-order: 3;
            order: 3;
  }
}

@media (min-width: @screen-xl) {
  
  .preview {
  
    &__title {
    
      padding-left: 356px;
      padding-right: 356px;
    }
  }

  .description-1,
  .description-2 {

    &-item {
      width: 400px;
      padding-top: 40px;
      background-color: hsl(203, 39%, 82%);
      color: #FFF;
      
      &__title {
        margin: 0;
        padding-left: 95px;
        padding-right: 95px;
      }
      
      &__number {
        padding-bottom: 20px;
      }

      &__preview {
        font-size: 16px;
      }
    }

    & .img {
      width: 800px;
    }

    & img {
      width: 800px;
      height: 256px;
    }
  }

  .description-2 {
    
    &-item__title {
      padding-left: 90px;
      padding-right: 90px;
    }
  }

  .description-3 {

    &-item{

      padding-left: 43px;
      padding-right: 43px;

      &__title {
        padding-left: 100px;
        padding-right: 100px;
      }
    
      &:nth-child(n+2) &__title {
        padding-left: 100px;
        padding-right: 100px;
      } 
    }
  }

  .own {
    
    &-item {
      width: 400px;
      padding-left: 30px;
      padding-right: 30px;
      
      & br {
        display: block;
      }

      &::before {
        left: 163px;
      }

      &__review {
        font-size: 14px;
      }
    }
  }
}