// Для импорта в диспетчер подключений: @import './src/blocks/page-header/page-header.less';

@import '../../less/variables.less';     // только для удобства обращения к переменным


.page-header-main {
  
  padding-bottom: 45px;
  background-color: #FFF;
    
  & .photo-svg {
    display: none;
  }
  
  &__title {
    position: relative;
  	width: 320px;
    height: 389px;
    background-color: hsl(0, 0, 100%);
    background-image: url(../img/back-mobile.jpg);
    background-repeat: no-repeat;
    -webkit-background-size: 320px 389px;
         -o-background-size: 320px 389px;
            background-size: 320px 389px;
    background-position: center;
    -webkit-box-sizing: border-box;
       -moz-box-sizing: border-box;
            box-sizing: border-box;
    
    
    &::before {
      content: "";
      position: absolute;
      top: 363px;
      left: 0;
      display: block;
      width: 320px;
      height: 26px;
      background-image: url(../img/intro-triangle-mobile.svg);
      background-repeat: no-repeat;
    }
  
  }

  & svg {
    width: 284px;
    height: 220px;
    margin: 0 18px;
    margin-top: 50px;
    fill: #fff;
  }
}

@media (min-width: @screen-md) {

  .page-header-main {

    & .photo-svg {
      display: none;
    }

    &__title {

      width: 768px;
      min-height: 518px;
      background-image: url(../img/back-tablet.jpg);
      background-repeat: no-repeat;
       -webkit-background-size: 768px 518px;
            -o-background-size: 768px 518px;
               background-size: 768px 518px;
       background-position: center;
    
      &::before {
        content: "";
        position: absolute;
        top: 461px;
        left: 0;
        display: block;
        width: 768px;
        height: 57px;
        background-image: url(../img/triangle-tablet.svg);
        background-repeat: no-repeat;
      }
    }

    & svg {
      width: 470px;
      height: 365px;
      margin: 78px 154px 0;
    }
  }
}

@media (min-width: @screen-xl) {

  .page-header-main {

    & .photo-svg {
      display: none;
    }

    &__title {
      width: 1200px;
      min-height: 518px;
      background-image: url(../img/back-desktop.jpg);
      background-repeat: no-repeat;
       -webkit-background-size: 1200px 518px;
            -o-background-size: 1200px 518px;
               background-size: 1200px 518px;
       background-position: center;
    
      &::before {
        content: "";
        position: absolute;
        top: 461px;
        left: -1px;
        display: block;
        width: 1201px;
        height: 57px;
        background-image: url(../img/triangle-desktop.svg);
        background-repeat: no-repeat;
      }
    }

    & svg {
      width: 465px;
      height: 354px;
      margin: 76px 369px 0;
    }
