.map {
	
	
	padding-bottom: 40px;
  background-color: @white;
  
  &__title {
    
    margin: 0;
    padding-top: 30px;
    padding-bottom: 20px;
    text-transform: @up;
    text-align: center;
  }

  &__order {
    margin: 0;
    padding-left: 10px;
    padding-right: 10px;
    margin-bottom: 25px;
    font-size: 14px;
    line-height: 28px;
    text-transform: @up;
    text-align: center;
    color: @gray-dark;
    
    & br {
      display: none;
    }
  }

  &__hotel-search {
    display: block;
    width: 280px;
    margin: 0 auto;
    margin-bottom: 30px;
    padding: 15px 40px;
    font-size: 21px;
    line-height: 26px;
    background-color: hsl(203, 39%, 82%);
    text-transform: @up;
    text-align: center;
    color: @white;
    
    &:hover {
      background-color: hsl(203, 47%, 75%);
      text-decoration: none;
    }

    &:active {
      color: hsl(202, 35%, 82%);
    }
  }

  &__wrapper {
    width: 320px;
    
    & img {
      width: 320px;
      height: 350px;
    }
  }
}



@media (min-width: @screen-md) {
  
  .map {

    &__title {
      font-weight: bold;
    }

    &__order {

      & br {
        display: inline-block;
      }
    }

    &__hotel-search {
      width: 458px;
    }
    
    &__wrapper {
      width: 768px;
    }

    & img {
      width: 768px;
      height: 350px;
    }
  }
}


@media (min-width: @screen-xl) {
  .map {

    padding-top: 30px;
    
    &__hotel-search {
      margin-bottom: 60px;
      margin-top: 60px;
    }

    &__wrapper {
      width: 1200px;
    }

    & img {
      width: 1200px;
      height: 350px;
    }
  }
  
}
