.forms {
	
	width: 320px;
	padding-top: 40px;
  padding-bottom: 75px;
	background-color: #FFF;
	
	&__title {
		
    margin: 0;
    padding-bottom: 35px;
    font-size: 18px;
    font-weight: bold;
    line-height: 24px;
    text-align: center;
    text-transform: uppercase;
    color: #000;
	}

	&__review {
		
		margin: 0;
    margin-bottom: 15px;
    padding-bottom: 18px;
    padding-left: 17px;
    padding-right: 17px;
    font-size: 14px;
    line-height: 21px;
    text-align: center;
    text-transform: uppercase;
    border-bottom: 1px solid @gray-lightest;
    color: #696969;
	}

  & ::-webkit-input-placeholder {color:#000;}
  & ::-moz-placeholder          {color:#000;}/* Firefox 19+ */
  & :-moz-placeholder           {color:#000;}/* Firefox 18- */
  & :-ms-input-placeholder      {color:#000;}

  input::-webkit-input-placeholder       {opacity: 1; transition: opacity 0.5s ease;}
  input::-moz-placeholder                {opacity: 1; transition: opacity 0.5s ease;}
  input:-moz-placeholder                 {opacity: 1; transition: opacity 0.5s ease;}
  input:-ms-input-placeholder            {opacity: 1; transition: opacity 0.5s ease;}
  input:focus::-webkit-input-placeholder {opacity: 0; transition: opacity 0.5s ease;}
  input:focus::-moz-placeholder          {opacity: 0; transition: opacity 0.5s ease;}
  input:focus:-moz-placeholder           {opacity: 0; transition: opacity 0.5s ease;}
  input:focus:-ms-input-placeholder      {opacity: 0; transition: opacity 0.5s ease;}

  textarea::-webkit-input-placeholder       {opacity: 1; transition: opacity 0.5s ease;}
  textarea::-moz-placeholder                {opacity: 1; transition: opacity 0.5s ease;}
  textarea:-moz-placeholder                 {opacity: 1; transition: opacity 0.5s ease;}
  textarea:-ms-input-placeholder            {opacity: 1; transition: opacity 0.5s ease;}
  textarea:focus::-webkit-input-placeholder {opacity: 0; transition: opacity 0.5s ease;}
  textarea:focus::-moz-placeholder          {opacity: 0; transition: opacity 0.5s ease;}
  textarea:focus:-moz-placeholder           {opacity: 0; transition: opacity 0.5s ease;}
  textarea:focus:-ms-input-placeholder      {opacity: 0; transition: opacity 0.5s ease;}

  & .footnote {
    display: none;
  }

  & input:hover,
  & textarea {
    background-color: #e5e5e5;
  }

  & input:focus {
    background-color: #FFF;
    outline: 2px solid #e5e5e5;
  }

  & textarea:focus {
    background-color: #FFF;
    outline: 2px solid #e5e5e5;
  }
}

.form {
  
  display: flex;
  -webkit-flex-direction: column;
      -ms-flex-direction: column;
          flex-direction: column;
  

  &__btn {
    
    -webkit-order: 6;
        -ms-order: 6;
            order: 6;
    display: block;
    width: 280px;
    margin: 0 15px;
    font-size: 18px;
    line-height: 58px;
    text-align: center;
    text-transform: uppercase;
    background-color: #81b3d2;
    border: none;
    outline: none;
    color: #FFF;
  }

  & .form__btn:hover {
    
    background-color: #669EC0;
  }

  & .form__btn:active {
    
    background-color: #5496BD;
    color: #88B6D1;
  }
}


.impression {
  
  -webkit-order: 3;
      -ms-order: 3;
          order: 3;
  margin: 0;
  margin-bottom: 20px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 20px;
  background-color: #FFF;
  border: none;
  border-bottom: 1px solid @gray-lightest;
  
  &__legend {
    
    padding-bottom: 45px;
    font-size: 18px;
    font-weight: bold;
    line-height: 26px;
    text-align: center;
    text-transform: uppercase;
    color: #000;
  }

  &__label {
    
    display: block;
    padding-left: 75px;
    padding-bottom: 45px;
    font-size: 18px;
    font-weight: bold;
    line-height: 26px;    
  }

  &__radio  {
  
    display: none;
  
    &--check {
    
      position: relative;
    
      &::before {
      
        content: "";
        position: absolute;
        top: -17px;
        left: -75px;
        display: block;
        width: 55px;
        height: 55px;
        -webkit-border-radius: 50%;
                border-radius: 50%;
        background-color: #f2f2f2;
      }

      &:hover::before {
        
        background-color: #e5e5e5;
      }

      &:active::before {
        
        background-color: #d5d5d5;
      }

      &::after {
      
        content: "";
        position: absolute;
        top: -1px;
        left: -58px;
        display: none;
        width: 22px;
        height: 22px;
        -webkit-border-radius: 50%;
                border-radius: 50%;
        background-color: #000;
      }

      &:hover::after {
        
        background-color: #000;
      }

      &:active::after {
        
        background-color: #a4a4a4;
      }
    }

    &:checked + &--check::after {
    
      display: block;
    }
  }
}



