  }

  &__label {
    
    display: inline-block;
    margin-bottom: 10px;
    font-size: 14px;
    line-height: 26px;
    text-transform: uppercase;
    color: @gray-dark;    
  }

  &__input {
    
    display: inline-block;
    width: 100%;
    margin-left: 15px;
    margin-bottom: 10px;
    padding-left: 15px;
    padding-right: 10px;
    -webkit-box-sizing: border-box;
       -moz-box-sizing: border-box;
            box-sizing: border-box;
    font-size: 18px;
