  }

  &__tel + span {

    position: relative;

    &::after {
    
      content: url(../img/icon-phone.svg);
      position: absolute;
      top: -50px;
      left: 243px;
      display: block;
      width: 20px;
      height: 20px;
    }

    &::before {

      content: '';
      position: absolute;
      top: -69px;
      left: 223px;
      display: block;
      width: 58px;
      height: 58px;
      background-color: rgba(0, 0, 0, .1);
    }

    &:hover::before {
        
      background-color: #e5e5e5;
    }

    &:active::before {
        
    background-color: #d5d5d5;
    }
  }

  &__email + span {
    
    position: relative;
    
    &::after {
      
      content: url(../img/icon-mail.svg);
      position: absolute;
      top: -50px;
      left: 243px;
      display: block;
      width: 20px;
      height: 20px;
    }

    &::before {

      content: '';
      position: absolute;
      top: -69px;
      left: 223px;
      display: block;
      width: 58px;
      height: 58px;
      background-color: rgba(0, 0, 0, .1);
    }

    &:hover::before {
        
      background-color: #e5e5e5;
    }

    &:active::before {
        
      background-color: #d5d5d5;
    }
  }

  & .error {
    
    display: block;
    margin-left: 15px;
    margin-bottom: 10px;
    font-size: 14px;
    color: red;
  }
}

