// Для импорта в диспетчер подключений: @import './src/blocks/page-footer/page-footer.less';

@import '../../less/variables.less';     // только для удобства обращения к переменным


.page-footer {
  
  background-color: #FFF;

  &__visit {
  	display: block;
  	margin: 0 auto;
    margin-bottom: 30px;
  	font-size: 21px;
  	font-weight: bold;
  	line-height: 26px;
  	text-transform: @up;
  	text-align: center;
  	color: @black;
  	
  	&:hover {
  		color: #81b3d2;
      text-decoration: none;
  	}

    &:active {
      color: @gray-lighter;
    }
  }

  &__developed {
    display: inline-block;
    width: 100%;
    margin: auto;
    padding-left: 90px;
    padding-right: 90px;
    padding-bottom: 20px;
    font-size: 14px;
    line-height: 26px;
    text-transform: @up;
    
    & a {
      display: inline-block;
      width: 27px;
      height: 35px;
      vertical-align: middle;
      
    }
  }

  & svg:hover {
    fill: #81b3d2;
  }

  & svg:active {
    fill: @gray-lightest;
  }
}

.social {
  
  .list();
  display: block;
  width: 150px;
  margin: 0 auto;
  margin-bottom: 40px;
  
  &__item {
    display: inline-block;
    margin-left: 1px;
    background-color: #81b3d2;
    font-size: 0;
    text-align: center;
    vertical-align: top;
    
    & a {
      display: block;
      width: 45px;
      height: 45px;
      padding-top: 8px;
      line-height: 45px;
      
      & svg path {
        fill: #fff;
      }
      
      &:hover {
        background-color: #669ec0;
      }

      &:active {
        background-color: #5496bd;
        color: #88b6d1;
        
        & svg path{
          fill: #88b6d1;
        }
      }
    }
  }

  & svg {
    fill: #ffffff;
  }
}


@media (min-width: @screen-md) {
  
  .page-footer {
    
    & .wrapper {
      display: flex;
      -webkit-justify-content: space-around;
              justify-content: space-around;
    }

    &__visit {
      display: block;
      width: 180px;
      flex-grow: 1;
      margin-left: 30px;
    }

    &__developed {
            
      display: block;
      width: 80px;
      margin-right: 90px;
      flex-grow: 2;
      
      & a {

        margin-left: 100px;
        margin-top: -50px;
      } 
   }
  }

  .social {
    display: flex;
    width: 180px;
      flex-grow: 2;
      margin-left: 110px;
  }

}


@media (min-width: @screen-xl) {
  
  .page-footer {
    
    & .wrapper {
      display: flex;
      -webkit-justify-content: space-between;
              justify-content: space-between;
    }

    &__visit {
      display: block;
      flex-grow: 1;
      margin-left: 30px;
    }

    &__developed {
            
      display: block;
      margin-right: -50px;
      flex-grow: 2;
      
      & a {

        margin-left: 100px;
        margin-top: -50px;
      } 
   }
  }

  .social {
    display: flex;
      flex-grow: 4;
      margin-left: 270px;
  }

}

