// Для импорта в диспетчер подключений: @import './src/blocks/main-nav/main-nav.less';

@import '../../less/variables.less';     // только для удобства обращения к переменным


.main-nav {

  & .opened {
    display: block;
  }

  & .closed {
    display: none;
    
    @media (min-width: @screen-md) {
      display: block;
    }
  }

  .row();
  
  position: relative;
  z-index: 1;
  //margin-left: (@grid-gutter-width / -2);
  //margin-right: (@grid-gutter-width / -2);

  &__list {

    -webkit-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
    width: 320px;
    padding: 0;
    margin: 0 auto;
    list-style: none;

    @media (min-width: @screen-md) {
      .row();
      display: flex;
      flex-wrap: nowrap;
      width: 768px;
      //-webkit-justify-content: center;
              //justify-content: center;
    }

    @media (min-width: @screen-xl) {
      width: 1200px;
    }
  }

  &__nested-lvl {

    @media (min-width: @screen-lg) {
      position: absolute;
      left: 0;
      top: 100%;
      background-color: @gray-lightest;
      padding-top: 0.3em;
      width: auto;
      border: 1px solid @gray-lighter;
      border-top: 0;
      box-shadow: @shadow;

      .main-nav__item:not(:last-child) {
        border-bottom: 1px solid @gray-lighter;
      }
    }

    .main-nav__list {
      display: block;
    }

    .main-nav__link {
      padding-left: @grid-gutter-width;
      min-width: 15em;

      @media (min-width: @screen-lg) {
        padding-left: (@grid-gutter-width / 2);
      }
    }

    // Вложение внутри вложения
    & & {

      @media (min-width: @screen-lg) {
        position: absolute;
        top: 0;
        left: 100%;
        background-color: @gray-lightest;
        width: auto;
        padding-top: 0;
        border-top: 1px solid @gray-lighter;

        .main-nav__link {
          padding-left: (@grid-gutter-width / 2);
        }
      }

      .main-nav__link {
        padding-left: @grid-gutter-width * 1.5;
      }
    }
  }

  &__item {
    display: block;
    position: relative;    
    background-color: hsl(203, 39%, 82%);
    border-bottom: 1px solid hsl(203, 39%, 66%);
    border-top: 1px solid hsl(203, 30%, 86%);
    

    @media (min-width: @screen-md){
      background-color: @white;
      border: none;
      
      &:hover {
        background-color: #FFF;
      }

      &:active {
        background-color: #FFF;
      }
    }

    &:nth-child(1) {
      -webkit-order: 1;
          -ms-order: 1;
              order: 1;
    }
    
    &:nth-child(2) {
      -webkit-order: 2;
          -ms-order: 2;
              order: 2;
    }
    
    &:nth-child(3) {
      -webkit-order: 4;
          -ms-order: 4;
              order: 4;
    }
    
    &:nth-child(4) {
      -webkit-order: 5;
          -ms-order: 5;
              order: 5;
    }
    
    &:nth-child(5) {
      -webkit-order: 3;
          -ms-order: 3;
              order: 3;
    }

    &:hover {   
      background-color: hsl(203, 47%, 75%);
    }

    &--divider {

      &:before {
        content: '';
        position: absolute;
        top: -1px;
        left: 0;
        right: 0;
        border-top: 1px solid @gray-light;
      }
    }

    // Сокрытие и показ по наведению
    @media (min-width: @screen-lg) {

      & > .main-nav__nested-lvl {
        display: none;
      }

      &:hover {

        & > .main-nav__nested-lvl {
          display: block;
        }
      }
    }
  }

  &__link {
    display: block;
    padding-top: 1.4rem;
    padding-bottom: 1.4rem;
    //padding-left: (@grid-gutter-width / 2);
    //padding-right: (@grid-gutter-width / 2);
    font-size: 14px;
    line-height: 26px;
    font-weight: bold;
    color: @white;
    text-decoration: none;
    text-transform: uppercase;
    cursor: pointer;
    text-align: center;

    &:hover,
    &:focus {
      //color: @text-color;
      background-color: hsl(203, 47%, 75%);
      text-decoration: none;
    }

    &:active {
      background-color: hsl(202, 56%, 74%);
      color: hsl(202, 35%, 82%);
    }
      
    @media (min-width: @screen-md) {
     color: @black;
     
     &:hover {
      background-color: #FFF;
       color: hsl(203, 39%, 82%);
     }

     &:active {
      background-color: #FFF;
       color: @gray-lighten;
     }
    }
  }

  &__logo {
    .clearfix();
    position: relative;
    min-height: 54px;
    background-color: #FFF;
    border-bottom: none;
    
    &:hover,
    &:active {
      background-color: #FFF;
    }
    
    & a {
      position: absolute;
      top: -1px;
      left: 50%;
      -webkit-transform: translate(-50%, 0);
         -moz-transform: translate(-50%, 0);
          -ms-transform: translate(-50%, 0);
           -o-transform: translate(-50%, 0);
              transform: translate(-50%, 0);
      
      display: block;
      width: 100px;
      height: 84px;
      margin: 0 auto;
      
      @media (min-width: @screen-md) {
       width: 102px;
       height: 84px;
       color: @black;
      }
      
      @media (min-width: @screen-xl) {
       width: 186px;
        height: 96px;
      }
    }
  }

  &__toggle-open {
    position: relative;
    float: right;
    display: block;
    width: 23px;
    height: 23px;
    margin-top: 16px;
    margin-right: 20px;
    padding: 0;
    border: none;
    outline: none;
    font-size: 0;
    cursor: pointer;
    background-color: transparent;
                        
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      width: 23px;
      height: 4px;
      background-color: hsl(203, 39%, 82%);
    
      box-shadow: 0 8px 0 0 hsl(203, 39%, 82%),
                  0 16px 0 0 hsl(203, 39%, 82%);
    }
     

    @media (min-width: @screen-md) {
      display: none;
    }
  }

  &__toggle-close {

    position: relative;
    float: right;
    z-index: 10;
    display: block;
    width: 42px;
    height: 32px;
    margin-top: -38px;
    margin-right: 20px;
    border: none;
    outline: none;
    font-size: 0;
    background-color: transparent;
    cursor: pointer;
    
    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 15px;
      left: 15px;
      display: block;
      width: 26px;
      height: 4px;
      background-color: #FFF;
    }

    &::before {
      -webkit-transform: rotate(45deg);
         -moz-transform: rotate(45deg);
          -ms-transform: rotate(45deg);
           -o-transform: rotate(45deg);
              transform: rotate(45deg);
    }

    &::after {
      -webkit-transform: rotate(-45deg);
         -moz-transform: rotate(-45deg);
          -ms-transform: rotate(-45deg);
           -o-transform: rotate(-45deg);
              transform: rotate(-45deg);
    }


    @media (min-width: @screen-md) {
      display: none;
    }
  }
}

